import React, { Fragment } from "react";
import { MdCancel, MdOutlineArrowForward } from "react-icons/md";

import { connect } from "react-redux";
import {
  Auth,
  Basic,
  district_schools_position_occupation,
  FC_GetFreeVacancies,
  FreeVacanciesInterface,
  Placement,
} from "../../actions";
import { FC_GetDistricts } from "../../actions/transfer.action";
import Alert from "../../components/Alert/Alert";
// import Button from "../../components/Buttons/Button";
import DashboardContainer from "../../components/DashboardContainer/DashboardContainer";
import Heading from "../../components/Heading/Heading";
import InputSelect from "../../components/Inputs/InputSelect";
import TextInput from "../../components/Inputs/TextInput";
import Loading from "../../components/Loading/Loading";
import { StoreState } from "../../reducers";
import { DistrictTypes } from "../../types/TransferTypes";
import { search } from "../../utils/functions";

// props for the component
interface AppProps {
  auth: Auth;
  basic: Basic;
  placement: Placement;
}

interface AppState {
  loading_district: boolean;
  error: {
    target: "main-success" | "main-error" | null;
    msg: string;
  };
  districts: DistrictTypes[];
  vacancies: FreeVacanciesInterface | null;
  selected_district: DistrictTypes | null;
  loading_vacancies: boolean;
  selected_qualification: string;
  search: string;
}

class _FreeVacancies extends React.Component<AppProps, AppState> {
  constructor(props: AppProps) {
    super(props);

    this.state = {
      loading_vacancies: false,
      loading_district: true,
      districts: [],
      vacancies: null,
      selected_district: null,
      error: {
        target: null,
        msg: "",
      },
      selected_qualification: "",
      search: "",
    };
  }

  componentDidMount = () => {
    FC_GetDistricts((status, data, msg) => {
      this.setState({
        loading_district: false,
        districts: data === null ? [] : data,
        error:
          data === null
            ? {
                target: "main-error",
                msg: "msg",
              }
            : {
                target: null,
                msg: "",
              },
      });
    });
  };

  selectDistrict = (district: DistrictTypes | null) => {
    if (district !== null) {
      this.setState({
        selected_district: district,
        loading_vacancies: true,
        vacancies: null,
      });

      FC_GetFreeVacancies(district.district_code, (status, data, msg) => {
        if (status === true) {
          this.setState({
            loading_vacancies: false,
            vacancies:
              data === null
                ? data
                : {
                    ...data,
                    district_schools_position_occupation:
                      data?.district_schools_position_occupation.filter(
                        (itm) => +itm.vacant >= 1
                      ),
                  },
          });
        } else {
          this.setState({
            loading_vacancies: false,
            vacancies: null,
            error: {
              target: "main-error",
              msg: msg,
            },
          });
        }
      });
    } else {
      this.setState({ selected_district: district });
    }
  };

  render() {
    if (this.state.loading_district === true)
      return (
        <div className="p-8 text-center">
          <div className="mx-auto">
            <Loading />
          </div>
        </div>
      );

    if (this.state.error.target === "main-error") {
      return (
        <Alert theme="danger" fixed>
          {this.state.error.msg}
        </Alert>
      );
    }

    let VacanciesData: district_schools_position_occupation[] = [];

    if (this.state.vacancies !== null) {
      VacanciesData =
        this.state.selected_qualification === ""
          ? this.state.vacancies.district_schools_position_occupation
          : this.state.vacancies.district_schools_position_occupation.filter(
              (itm) =>
                itm.qualification_id === this.state.selected_qualification
            );

      // Search data
      VacanciesData = search(VacanciesData, this.state.search, {
        position_code: true,
        position_name: true,
        qualification_name: true,
        school_code: true,
        school_name: true,
      });
    }

    return (
      <>
        {this.state.error.target === "main-success" && (
          <Alert theme="success" fixed>
            {this.state.error.msg}
          </Alert>
        )}

        <>
          <div className="sticky top-14">
            <DashboardContainer>
              <Heading className="text-center pb-2">
                Available vacancies
              </Heading>
              {this.state.selected_district !== null && (
                <div className="border-t p-1 flex pt-2">
                  <div className="pt-1 flex items-center">
                    <div className="border rounded flex pl-2">
                      <h2 className="p-2 font-bold text-xl">
                        {this.state.selected_district.district_name} district
                      </h2>
                      <button
                        className="p-2 py-3 hover:bg-red-200 text-red-500 rounded"
                        onClick={this.selectDistrict.bind(this, null)}
                      >
                        <MdCancel className="text-2xl" />
                      </button>
                    </div>
                  </div>

                  <div className="flex items-center mx-2 px-2 border rounded">
                    <label htmlFor="" className="text-lg pr-2 ">
                      Qualification:{" "}
                    </label>
                    <InputSelect
                      size="sm"
                      onChange={(event: React.ChangeEvent<HTMLSelectElement>) =>
                        this.setState({
                          selected_qualification: event.target.value,
                        })
                      }
                      value={this.state.selected_qualification}
                      error={""}
                      label={""}
                      placeholder={"Select Qualification"}
                      name={""}
                      data={[
                        { title: "Select Qualification", value: "" },
                        ...(this.state.vacancies
                          ? this.state.vacancies?.qualifications.map((itm) => ({
                              title: itm.qualification_name,
                              value: itm.qualification_id,
                            }))
                          : []),
                      ]}
                    />
                  </div>

                  <div className="mx-2 flex items-center border px-2 rounded">
                    <label className="text-lg pr-2">Search</label>
                    <TextInput
                      error=""
                      label=""
                      name=""
                      size="sm"
                      onChange={(e) =>
                        this.setState({ search: e.target.value })
                      }
                      placeholder="Search"
                      value={this.state.search}
                    />
                  </div>
                </div>
              )}
            </DashboardContainer>
          </div>

          {this.state.selected_district === null ? (
            <DashboardContainer>
              <div>
                <h2 className="text-center p-2 text-gray-400 pb-4 text-2xl">
                  Please select a district
                </h2>

                {this.state.districts.length >= 1 ? (
                  <div className="grid grid-cols-2 gap-2 ">
                    {this.state.districts.map((dist) => (
                      <div
                        onClick={this.selectDistrict.bind(this, dist)}
                        key={dist.district_code}
                        className="p-2 flex cursor-pointer rounded border  px-3 hover:text-blue-600 hover:border-blue-600 hover:bg-blue-100 bg-white text-lg"
                      >
                        <h4 className="flex-1 text-lg">
                          {" "}
                          <b>{dist.district_name}</b> district
                        </h4>
                        <button className="">
                          <MdOutlineArrowForward />
                        </button>
                      </div>
                    ))}
                  </div>
                ) : (
                  <div>
                    <h2 className="text-center p-4 text-gray-400  text-2xl">
                      Something went wrong, <br /> please try again later
                    </h2>
                  </div>
                )}
              </div>
            </DashboardContainer>
          ) : (
            <DashboardContainer>
              {this.state.loading_vacancies === true ? (
                <div className="text-center py-10">
                  <Loading />
                </div>
              ) : (
                <div>
                  {this.state.vacancies === null ? (
                    <Alert theme="danger">Failed to load the data</Alert>
                  ) : (
                    <table className="w-full border">
                      <thead className="text-left">
                        <tr>
                          <th className="p-2 border">#</th>
                          <th className="p-2 border">School code</th>
                          <th className="p-2 border">School name</th>
                          <th className="p-2 border">Position code</th>
                          <th className="p-2 border">Position name</th>
                          <th className="p-2 border">Qualification</th>
                          <th className="p-2 border">Vacant</th>
                        </tr>
                      </thead>
                      <tbody>
                        {VacanciesData.length <= 0 ? (
                          <tr>
                            <td colSpan={8} className="text-center py-8">
                              <Heading>No results found</Heading>
                              <p className="pt-1 text-gray-600">
                                Please search using:{" "}
                                <b>
                                  school code, school name, position code,
                                  position name
                                </b>
                              </p>
                            </td>
                          </tr>
                        ) : (
                          VacanciesData.map((itm, i) => (
                            <tr key={i}>
                              <td className="p-2 border">{i + 1}</td>
                              <td className="p-2 border">{itm.school_code}</td>
                              <td className="p-2 border">{itm.school_name}</td>
                              <td className="p-2 border">
                                {itm.position_code}
                              </td>
                              <td className="p-2 border">
                                {itm.position_name}
                              </td>
                              <td className="p-2 border">
                                {itm.qualification_name}
                              </td>
                              <td className="p-2 border">
                                {+itm.vacant < 0 ? 0 : itm.vacant}
                              </td>
                            </tr>
                          ))
                        )}
                      </tbody>
                    </table>
                  )}
                </div>
              )}
            </DashboardContainer>
          )}
        </>
      </>
    );
  }
}

const mapStateToProps = ({
  auth,
  basic,
  placement,
}: StoreState): { auth: Auth; basic: Basic; placement: Placement } => {
  return { auth: auth, basic: basic, placement: placement };
};

export const FreeVacancies = connect(mapStateToProps, {})(_FreeVacancies);
